import { afternoonSnacks } from "@/lib/data/snacks";

export default function MeriendasPage() {
  return (
    <div className='container py-6'>
      <h1 className='text-3xl font-bold mb-6'>Opciones de Merienda</h1>
      <div className='grid gap-6'>
        {afternoonSnacks.map((snack) => (
          <article
            key={snack.id}
            className='p-6 bg-card text-card-foreground rounded-lg border'
          >
            <h2 className='text-2xl font-semibold mb-4'>{snack.title}</h2>

            {/* Tiempo y Porciones */}
            <div className='flex gap-4 text-sm text-muted-foreground mb-6'>
              {snack.preparationTime && (
                <p>⏱️ Tiempo: {snack.preparationTime}</p>
              )}
              {snack.servingSize && <p>🍽️ Rinde: {snack.servingSize}</p>}
            </div>

            {/* Ingredientes */}
            <div className='mb-6'>
              <h3 className='font-semibold mb-2'>Ingredientes:</h3>
              <ul className='list-disc pl-5 space-y-1'>
                {snack.ingredients.map((ingredient, index) => (
                  <li key={index}>{ingredient}</li>
                ))}
              </ul>
            </div>

            {/* Instrucciones */}
            <div className='mb-6'>
              <h3 className='font-semibold mb-2'>Preparación:</h3>
              <ol className='list-decimal pl-5 space-y-1'>
                {snack.instructions.map((step, index) => (
                  <li key={index}>{step}</li>
                ))}
              </ol>
            </div>

            {/* Beneficios */}
            <div className='mb-6'>
              <h3 className='font-semibold mb-2'>Beneficios:</h3>
              <ul className='list-disc pl-5 space-y-1'>
                {snack.benefits.map((benefit, index) => (
                  <li key={index}>{benefit}</li>
                ))}
              </ul>
            </div>

            {/* Etiquetas */}
            {snack.healthGoals && (
              <div className='flex gap-2'>
                {snack.healthGoals.map((goal) => (
                  <span
                    key={goal}
                    className='px-2 py-1 bg-primary/10 text-primary text-sm rounded-full'
                  >
                    {goal === "lowCholesterol"
                      ? "Bajo en Colesterol"
                      : goal === "immuneSystem"
                        ? "Sistema Inmune"
                        : "Equilibrado"}
                  </span>
                ))}
              </div>
            )}
          </article>
        ))}
      </div>
    </div>
  );
}
